:root {
  /*--vc-accent-color defined in index-vars.css*/
  --vc-secondary-accent-color: #D0021B;
  --vc-primary-fg-color: #0065AF;
  --vc-secondary-fg-color: #5D5D5D;
  --vc-tertiary-fg-color: #979797;
  --vc-primary-bg-color: #383838;
  --vc-secondary-bg-color: #D8D8D8;
  --vc-tertiary-bg-color: #F2F2F2;
  --vc-primary-border-color: #ABABAB;
  --vc-white-color: white;
  --vc-black-color: black;
  --vc-hover-accent-color: rgba(0, 102, 179, 0.3);
}

:root div::-webkit-scrollbar {
  width: 7px;
}
:root div::-webkit-scrollbar-thumb {
  background-color: var(--vc-secondary-bg-color);
}
:root div::-webkit-scrollbar-track {
  background-color: var(--vc-white-color);
}

:root div {
  scrollbar-color: var(--vc-secondary-bg-color) var(--vc-white-color);
  scrollbar-width: thin;
}

body {
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body .p-inputtext, body .p-inputtext:enabled:focus {
  background-color: var(--vc-tertiary-bg-color);
  border: none;
  box-shadow: inset 0 -1px 0 0 var(--vc-tertiary-bg-color);
}

body .p-inputtextarea {
  resize: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
