.dts {
    display: flex;
    height: 100%;
}

.dts-container {
    display: flex;
    flex-direction: column;
    background-color: var(--vc-tertiary-bg-color);
    min-width: 300px;
    width: 300px;
}

.dts-container.dts-minimized {
    width: 75px !important;
    min-width: 75px;
}

.dts-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1em;
    background-color: var(--vc-primary-bg-color);
    color: var(--vc-white-color);
    padding-left: 15px;
    height: 50px;
	z-index: 1;
}

.dts-header > img {
    padding: 20px 30px 20px 15px;
    cursor: pointer;
}

.dts-content {
    display: flex;
    border-left: 1px solid var(--vc-secondary-bg-color);
    height: 100%;
    overflow-y: auto;
}

.dts-steps {
    color: var(--vc-primary-bg-color);
    text-align: center;
    width: 100%;
    padding: 25px 5px 5px 10px;
    overflow-y: auto;
}

.dts-steps.dts-minimized {
    margin-left: 10px;
}

.dts-steps-footer {
    height: 25px;
}

.dts-step {
    display: flex;
    align-items: center;
    margin: 0px 15px 0px 0px;
}

.dts-step-icon {
    display: flex;
    align-items: center;
    border: 1px solid var(--vc-primary-bg-color);
    border-radius: 50%;
    margin-right: 10px;
    width: 32px;
    height: 32px;
}

.dts-step-icon > img {
    width: 30px;
    height: 30px;
    padding: 5px;
    cursor: auto;
    filter: invert(74%) sepia(4%) saturate(10%) hue-rotate(195deg) brightness(92%) contrast(87%);
}

.dts-step-label {
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.dts-separator {
    background-color: var(--vc-primary-bg-color);
    height: 15px;
    width: 1px;
    margin-left: 15px;
}

.dts-step.dts-processing > span > img {
    animation:rotate 1s linear infinite;
    filter: none;
}

.dts-step.dts-processing > span{
    color: var(--vc-accent-color);
}

@keyframes rotate{
  0%{transform:rotateZ(0deg)}
  100%{transform:rotateZ(360deg)}
}
