
.dialer-field {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 10px;
}

.dialer-button-bar {
    display: flex;
    justify-content: flex-end;
}

.dialer-button-bar .p-button {
    background-color: var(--vc-accent-color);
    border: 1px solid var(--vc-accent-color);
    font-size: 12px;
    border-radius: unset;
}

/* Custom overlayPanel to be displayed on the right of its target */
.p-overlaypanel {
    margin-top: -65px;
    margin-left: 45px;
    width: 220px;
    top: unset !important;
    left: unset !important;
    font-weight: normal;
}

.p-overlaypanel .p-inputtext{
    width: 100%;
    font-size: 12px;
    padding: 8px;
}

.p-overlaypanel label {
    font-size: 11px;
}

.p-overlaypanel:after {
    border-width: 10px;
	margin-top: -10px;
}

.p-overlaypanel:before {
    border-width: 10px;
	margin-top: -10px;
}

.p-overlaypanel-flipped:after, .p-overlaypanel-flipped:before {
    left: -10px;
    top: 45px;
    transform: rotate(90deg);
}
