/* 
* Virtual Client
*/
.vc {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--vc-white-color);
  height: 100vh;
  min-width: 1000px;
  min-height: 820px;
}

.vc-container {
  display: flex;
  justify-content: space-between;
  height: 100%;
  overflow-y: auto;
}

.vc-content {
  min-width: 625px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.vc-viewer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.vc-viewer-closed {
  opacity: 0.5;
  pointer-events: none;
}

header {
  display: flex;
  align-items: center;
  background-color: var(--vc-accent-color);
  min-height: 50px;
  z-index: 1;
}

header > span.vc-logo {
  background-image: var(--vc-logo);
  background-repeat: no-repeat;
  background-position: 20px 50%;
  height: 100%;
  width: 100%;
}

.vc-title {
  display: flex;
  justify-content: center;
  color: var(--vc-primary-bg-color);
  opacity: 0.6;
  font-weight: bold;
  font-size: 1.4em;
  margin-top: 10px;
}

.vc-error {
  margin-top: 10px;
  text-align: center;
}
