/*
* Device Viewer
*/
.dv {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 0px 0px 5px 5px;
  background-color: var(--vc-black-color);;
}

.dv-content {
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 4px var(--vc-primary-border-color);
  -webkit-box-shadow: 0 0 4px var(--vc-primary-border-color);
}

.dv-content.dv-processing {
  cursor: progress;
}

.dv-content.dv-portrait > img {
  height: 600px;
}

.dv-content.dv-landscape > img {
  width: 600px;
}

.dv-content.dv-hidden {
  visibility: hidden;
}

.dv-content.dv-disabled {
  opacity: 0.8;
  pointer-events: none;
}

.dv-selected-control {
  position: absolute;
  background: var(--vc-primary-fg-color);
  opacity: 0.3;
}

/* Progress bar */
.dv-pb-text{ 
  text-align: center;
  color: var(--vc-primary-bg-color);
  margin: -50px 20px 20px 20px;
  font-size: 18px;
  opacity: 0.8;
}

.dv-pb-container {
  background-color: var(--vc-secondary-bg-color);
  text-align: center;
  width: 228px;
  height: 6px;
  border-radius: 5px;
}

.dv-pb-content {
  background: var(--vc-accent-color);
  height: 6px; 
  width: 80px;
  border-radius: 5px;
  animation: increase 3s infinite;
}

@keyframes increase {
  0% {transform: translateX(0);}
  50% {transform: translateX(148px);} 
  100% {transform: translateX(0);} 
}

/* Keyboard controller */
.dv textarea {
  position: absolute;
  background-color: transparent;
  border: none;
  resize: none;
  width: 0px;
  height: 0px;
  overflow: hidden;
  z-index: -1;
  opacity: 0;
}
