.dnb {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--vc-primary-bg-color);
  border-radius: 0px 0px 5px 5px;
  height: 32px;
  box-shadow: 0 0 4px var(--vc-primary-border-color);
  -webkit-box-shadow: 0 0 4px var(--vc-primary-border-color);
}

.dnb img {
  width: 18px;
  height: 18px;
  cursor: pointer;
}
