.tb {
    background-color: var(--vc-primary-bg-color);
    height: 100%;
    min-width: 75px;
    text-align: center;
    z-index: 1;
}

.tb-content {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 500px;
}

.tb-content-ios {
    height: 350px;
}

.tb-disabled {
    pointer-events: none;
}

.tb-disabled .tb-button {
    opacity: 0.3;
}

.tb-button {
    width: 46px;
    height: 46px;
    padding: 11px;
    border-radius: 50%;
    cursor: pointer;
}

.tb-button img {
    width: 24px;
    height: 24px;
}

.tb-button-disabled {
    opacity: 0.3;
    cursor: default;
}

.tb-separator {
    background-color: var(--vc-secondary-fg-color);
    opacity: 0.82;
    width: 40px;
    height: 1px;
}

.tb-button:not(.tb-button-disabled):hover {
    background-color: var(--vc-hover-accent-color);
}
