.notifier, .notifier.info {
    --vc-notification-color: #0066B3;
}

.notifier.success {
    --vc-notification-color: #5aa700;
}

.notifier.warning {
    --vc-notification-color: #EFC100;
}

.notifier.error {
    --vc-notification-color: #D0021B;
}

.notifier {
    display: flex;
    align-items: center;
    background-color: white;
    border: 1px solid var(--vc-notification-color);
    border-left: 5px solid var(--vc-notification-color);
    min-height: 50px;
    font-size: 14px;
    z-index: 1;
}

.notifier span {
    margin-right: 10px;
}

.notifier img {
    color: var(--vc-notification-color);
    padding: 2px 20px 0px 20px;
}

.notifier-title {
    font-weight: 500;
}

.notifier-message {
    color: var(--vc-secondary-fg-color);
}

.notifier-close {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    cursor: pointer;
}

.notifier-hidden {
    visibility: hidden;
    opacity: 0;
    transition: 1s ease-out;
}
